import React from 'react';
import { Link } from 'react-router-dom';

export default data => {
  return (
    <div className="card" >
      <Link to={'/recipe/'+data.id} className="image">
        <img src={'/'+data.image} width={300} loading="lazy"/>
      </Link>

      <div className="meta">
        <div className="source">{
          data.sourceName
        }</div>
        <div className="title">{data.title}</div>
      </div>
    </div>
  );
};
