import React from 'react';
import ads from '../ads';

export default () => (
  <div className="section ads">
    <h1> Support Us and Shop!</h1>
    <div className="grid">
      { Object
          .values(ads)
          .flatMap(x=>x)
          .map( (x, k) =>
        <div className="ad card" >
          <div dangerouslySetInnerHTML={{__html: x}} key={k}/>
        </div>
      )}
    </div>
  </div>
);